// v-loadmore: element-uiselect
Vue.directive('scroll', {
    bind(el, binding) {
      // element-uiscroll
      const dom = el.querySelector('.el-table__body-wrapper');
      
      dom.addEventListener('scroll', function() {

          /*
           * scrollHeight ()
           * scrollTop ,, , , scrollTop0.
           * clientHeight ()
           * , true, false:
           * ele.scrollHeight - ele.scrollTop === ele.clientHeight;
           */
          console.log()
            // let sign = 80; // 
            // const CONDITION = ((this.scrollHeight - this.scrollTop -80=== this.clientHeight) && 
            //                 this.scrollTop >sign)// : && this.scrollTop
            const CONDITION = ((  this.clientHeight+ this.scrollTop>this.scrollHeight*0.9) )// : && this.scrollTop
            // console.log(this.scrollHeight)
            // console.log(this.clientHeight)
            // console.log(this.scrollTop)
            // if(this.scrollTop > sign) {
              
            //     sign = this.scrollTop;
            //     console.log('')
            // }
            // if(this.scrollTop < sign) {
            //     sign = this.scrollTop;
            //     console.log('')
            // }
            
            if(CONDITION) {
                binding.value();
            }
      });
    }
    
})

